* {
  box-sizing: border-box; }

.editor {
  display: flex;
  margin: 0 auto;
  width: 800px;
  height: calc(100vh - 20px);
  overflow-y: auto; }

.panel.left {
  display: flex;
  flex-flow: column;
  margin-right: 20px;
  width: 280px;
  border: 1px solid #666666; }

.container.record {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
  flex-shrink: 0;
  height: calc(100% - 40px);
  overflow-y: auto; }

.item.record {
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  padding: 10px;
  border-bottom: 1px solid #666666; }
  .item.record:hover {
    cursor: pointer;
    background: rgba(67, 150, 255, 0.56); }
  .item.record .name {
    flex-grow: 1; }
  .item.record .arrow {
    flex-grow: 0;
    width: 40px;
    text-align: right; }

.add.record {
  flex-grow: 0;
  flex-shrink: 0;
  padding: 10px;
  height: 40px;
  border-top: 1px solid #666666; }
  .add.record:hover {
    cursor: pointer;
    background: rgba(67, 255, 100, 0.6); }

.panel.right {
  display: flex;
  flex-flow: column;
  width: 500px;
  border: 1px solid #666666; }
  .panel.right .header {
    display: flex;
    flex-flow: row;
    flex-shrink: 0;
    padding: 10px;
    height: 60px;
    border-bottom: 1px solid #666666; }
    .panel.right .header .name {
      flex-grow: 1;
      flex-shrink: 1;
      margin-right: 10px;
      padding: 5px 10px;
      border: 1px solid #CCCCCC;
      font-size: 20px;
      min-width: 250px; }
    .panel.right .header button {
      flex-grow: 0;
      flex-shrink: 0;
      width: 40px;
      padding: 10px;
      text-align: center;
      border: 1px solid #CCCCCC;
      background: transparent;
      margin-right: 10px; }
    .panel.right .header .delete {
      margin: 0; }
      .panel.right .header .delete:hover {
        background: rgba(255, 60, 60, 0.6); }
    .panel.right .header .selected {
      background: rgba(67, 150, 255, 0.56); }

.container.entry {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
  flex-shrink: 0;
  height: calc(100% - 100px);
  overflow-y: auto; }

.item.entry {
  display: flex;
  flex-flow: row;
  flex-shrink: 0;
  align-items: start;
  padding: 10px;
  border-bottom: 1px solid #666666; }
  .item.entry:hover {
    background: rgba(67, 150, 255, 0.56); }
  .item.entry .page {
    width: 60px;
    flex-grow: 0; }
    .item.entry .page input {
      width: 100%;
      padding: 5px 10px;
      text-align: right;
      border: 1px solid #CCCCCC; }
    .item.entry .page .id {
      display: block;
      width: 100%;
      padding: 5px 10px;
      text-align: right; }
  .item.entry .center {
    flex-grow: 1;
    margin: 0 10px; }
  .item.entry .text textarea, .item.entry .summary textarea {
    width: 100%;
    max-width: 358px;
    padding: 5px 10px;
    border: 1px solid #CCCCCC; }
  .item.entry .delete {
    flex-grow: 0;
    width: 40px;
    padding: 10px;
    text-align: center;
    border: 1px solid #CCCCCC;
    background: transparent; }
    .item.entry .delete:hover {
      cursor: pointer;
      background: rgba(255, 60, 60, 0.6); }

.add.entry {
  flex-shrink: 0;
  padding: 10px;
  height: 40px;
  border-top: 1px solid #666666; }
  .add.entry:hover {
    cursor: pointer;
    background: rgba(67, 255, 100, 0.6); }

.export {
  position: fixed;
  top: 0;
  right: 0;
  font-size: 20px;
  background: #DDDDDD;
  padding: 10px 20px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  transform: rotate(-90deg);
  transform-origin: bottom right; }

.instructions {
  margin: 10px auto;
  padding: 10px;
  width: 800px;
  background: beige; }

